"use client";

import { useEffect, useState } from "react";
import { useSearchParams } from "next/navigation";
import { CityPhaser } from "@/components/CityPhaser";
import { OnlinePanel } from "@/components/OnlinePanel";
import { InventoryPanel } from "@/components/InventoryPanel";
import { ChatPanel } from "@/components/ChatPanel";
import { BottomMenu } from "@/components/BottomMenu";
import { getJSON } from "@/lib/clientApi";
import type { Character } from "@/lib/models";

export default function CityPage() {
  const searchParams = useSearchParams();
  const initialOwnerId = searchParams.get("ownerId") ?? "";
  const initialCharacterId = searchParams.get("characterId") ?? "";

  const [ownerId, setOwnerId] = useState(initialOwnerId);
  const [characterId, setCharacterId] = useState(initialCharacterId);
  const [characterInfo, setCharacterInfo] = useState<Character | null>(null);
  const [feedback, setFeedback] = useState<string | null>(null);
  const [loadingCharacter, setLoadingCharacter] = useState(false);

  useEffect(() => {
    setOwnerId(initialOwnerId);
    setCharacterId(initialCharacterId);
    if (initialOwnerId && initialCharacterId) {
      void loadSelectedCharacter(initialOwnerId, initialCharacterId);
    }
  }, [initialOwnerId, initialCharacterId]);

  async function loadSelectedCharacter(currentOwnerId: string, currentCharacterId: string) {
    if (!currentOwnerId || !currentCharacterId) {
      setCharacterInfo(null);
      return;
    }
    setLoadingCharacter(true);
    setFeedback(null);
    try {
      const data = await getJSON<Character>(
        `/api/character/get?ownerId=${currentOwnerId}&characterId=${currentCharacterId}`
      );
      setCharacterInfo(data);
    } catch (err) {
      setCharacterInfo(null);
      setFeedback(getMessage(err));
    } finally {
      setLoadingCharacter(false);
    }
  }

  return (
    <section>
      <div className="card">
        <h2>Etapas 2 e 3 — Cidade com Phaser e Movimentação Básica</h2>
        <p>
          Esta é a primeira área após o login. Entre aqui somente através da tela inicial (botão
          Entrar no primeiro mapa), que envia automaticamente o personagem escolhido.
        </p>
        {!ownerId || !characterId ? (
          <p>Retorne à página inicial e selecione um personagem para receber acesso à cidade.</p>
        ) : (
          <>
            {loadingCharacter && <p>Carregando personagem selecionado…</p>}
            {feedback && <p>{feedback}</p>}
            {characterInfo && (
              <div style={{ marginTop: 12 }}>
                <p>
                  Personagem ativo: <strong>{characterInfo.name}</strong> (sprite {characterInfo.sprite}
                  ) — nível {characterInfo.stats.level}
                </p>
              </div>
            )}
          </>
        )}
      </div>
      <div className="card">
        <p>
          O piso utiliza os tiles <code>public/tilesets/tile1.png</code> até <code>tile6.png</code>
          (100×100), todos reamostrados para blocos de 32 px no Phaser. O mapa foi ampliado e continua
          responsivo — abra em tela cheia para explorar melhor.
        </p>
        <CityPhaser />
      </div>
      {ownerId && (
        <div className="grid">
          <div className="card">
            <OnlinePanel ownerId={ownerId} />
          </div>
          <div className="card">
            <InventoryPanel ownerId={ownerId} />
          </div>
        </div>
      )}
      <div className="card">
        <ChatPanel ownerId={ownerId} />
      </div>
      {ownerId && <BottomMenu />}
    </section>
  );
}

function getMessage(err: unknown) {
  return err instanceof Error ? err.message : "Falha ao carregar personagem";
}
