"use client";

import { useEffect, useState } from "react";
import { getJSON, postJSON } from "@/lib/clientApi";
import type { ChatMessage } from "@/lib/models";

export function ChatPanel({
  ownerId: forcedOwnerId,
  characterName
}: {
  ownerId?: string;
  characterName?: string;
}) {
  const [ownerId, setOwnerId] = useState(forcedOwnerId ?? "");
  const [name, setName] = useState(characterName ?? "");
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    let active = true;
    async function load() {
      try {
        const list = await getJSON<ChatMessage[]>("/api/chat/get");
        if (active) {
          setMessages(list);
        }
      } catch (err) {
        if (active) {
          setError(getMessage(err));
        }
      }
    }

    load();
    const interval = setInterval(load, 3000);
    return () => {
      active = false;
      clearInterval(interval);
    };
  }, []);

  useEffect(() => {
    if (forcedOwnerId) {
      setOwnerId(forcedOwnerId);
    }
  }, [forcedOwnerId]);

  useEffect(() => {
    if (characterName) {
      setName(characterName);
    }
  }, [characterName]);

  async function handleSend(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    if (!ownerId) {
      setError("Informe o ownerId para enviar ao chat.");
      return;
    }
    try {
      await postJSON("/api/chat/send", { ownerId, name: name || undefined, message });
      setMessage("");
      setError(null);
    } catch (err) {
      setError(getMessage(err));
    }
  }

  return (
    <div>
      <h3>Chat Global</h3>
      {error && <p>{error}</p>}
      <form onSubmit={handleSend}>
        {!forcedOwnerId && (
          <>
            <label htmlFor="chat-owner">Owner ID</label>
            <input
              id="chat-owner"
              value={ownerId}
              onChange={(event) => setOwnerId(event.target.value)}
              required
            />
          </>
        )}
        {!characterName && (
          <>
            <label htmlFor="chat-name">Nome do personagem</label>
            <input
              id="chat-name"
              value={name}
              onChange={(event) => setName(event.target.value)}
              placeholder="Herói"
            />
          </>
        )}
        <label htmlFor="chat-message">Mensagem</label>
        <textarea
          id="chat-message"
          value={message}
          maxLength={280}
          onChange={(event) => setMessage(event.target.value)}
          required
        />
        <button className="button">Enviar</button>
      </form>
      <ul className="list" style={{ marginTop: 16 }}>
        {messages.map((entry) => (
          <li key={entry.id}>
            <strong>{entry.characterName ?? entry.ownerId}</strong>: {entry.message}{" "}
            <small>{new Date(entry.createdAt).toLocaleTimeString()}</small>
          </li>
        ))}
        {messages.length === 0 && <li>Nenhuma mensagem.</li>}
      </ul>
    </div>
  );
}

function getMessage(err: unknown) {
  return err instanceof Error ? err.message : "Falha no chat";
}
